import { defineComponent, ref, useContext, useRoute } from '@nuxtjs/composition-api';
export default defineComponent({
    name: 'DebugPage',
    layout: 'error',
    setup() {
        const { app } = useContext();
        const route = useRoute();
        const getUrl = () => {
            return route.value.fullPath;
        };
        const localStorageKey = ref('deeplink');
        const localStorageValue = ref('');
        const useragents = ref();
        const message = ref('Sample Error Captured');
        const getLocalStorageValue = (key) => {
            localStorageValue.value = localStorage.getItem(key) || '';
        };
        const sendToSentry = (msg) => {
            app.$sentry.captureException(new Error(msg));
        };
        const getUserAgents = () => {
            useragents.value = app.$ua;
        };
        return {
            app,
            localStorageValue,
            route,
            getUrl,
            getUserAgents,
            useragents,
            getLocalStorageValue,
            sendToSentry,
            message,
            localStorageKey
        };
    }
});
